@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  margin: 0;
  padding: 0;
  font-family: 'Orbitron', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  background: #c6e2e9;
  width: 100vw;
  height: 100vh;
  align-items: center;
  flex-direction: column;
  font-size: 70%;
}
.title {
    font-size: 600%;
    font-weight: 200;
    color: #2a324b;
    margin: 8%;
    text-align: center;
    margin: 4%;
}

header {
    width: 60vw;
}

@media screen and (max-width: 1200px){
    .title {
        font-size: 550%;
    }
}

@media screen and (max-width: 470px){
    .title {
        font-size: 360%;
    }
}
main {
    width: 40vw;
    height: 75vh;
    background: #bfcfdf;
    border-radius: 5%;
    display: flex;
    flex-wrap: wrap;
}

@media screen and (max-width: 1200px){
    main {
        width: 80vw;
    }
}

@media screen and (max-width: 900px){
    main {
        font-size: 75%;
    }
}
.output-container {
    height: 20%;
    width: 100%;
}

.output {
    width: 90%;
    border-top-right-radius: 5%;
    border-top-left-radius: 5%;
    font-size: 650%;
    background-color: #98adc0;
    outline: none;
    border: none;
    color: whitesmoke;
    font-weight: 100;
    padding-left: 5%;
    padding-right: 5%;
    height: 100%;
}

input:focus {
    outline: none;
}
.button {
    font-size: 550%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    height: 16%;
    cursor: pointer;
    font-weight: 200;
    outline: 1.5px solid #c6e2e9;
    border-radius: 2%;
}

.button:active {
    -webkit-transition-duration: 125ms;
            transition-duration: 125ms;
    -webkit-transform: translateY(1.5px);
            transform: translateY(1.5px);
}

.button-ac {
    background-color: #2a324b;
    color: white;
    width: 75%;
}
.button-operations {
    background-color: #ffcaaf;
    width: 25%;
}

.button-numbers {
    background-color: #bfcfdf;
    width: 25%;
}

.button-zero {
    width: 50%;
    border-bottom-left-radius: 15%;
}

.button-equals {
    border-bottom-right-radius: 20%;
}
